* {
  list-style: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  background-color: cornflowerblue;
  min-height: 100vh;
  width: 100vw;
}
.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card {
  background-color: whitesmoke;
  border-radius: 25px;
  box-shadow: 0 0 15px darkblue;
  min-width: 170px;
  margin: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
}
.name {
  background-color: lightgreen;
  color: white;
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
}
.tagsList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.typeTag {
  font-size: 12px;
  background-color: lightblue;
  border-radius: 10px;
  text-align: center;
  padding: 3px 5px;
  margin: 5px 2px;
}
